.tarjeta_ultimos {
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.4);
    cursor: pointer;
}

.tarjeta_ultimos h2 {
    position: absolute;
    z-index: 1;
    width: 100%;
    top: 0;
    padding: 5px;
    font-size: .8rem;
    font-weight: 500;
    background: rgba(0, 0, 0, 0.8);

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.tarjeta_ultimos img {
    width: 100%;
    transform: scale(1.05) rotate(0);
    transition: all .6s ease;
}

.tarjeta_ultimos img:hover {
    transform: scale(1.1) rotate(-4deg);
}

.tarjeta_ultimos h4 {
    position: absolute;
    width: 100%;
    bottom: 0;
    padding: 2px;
    font-size: .8rem;
    background: rgba(24, 24, 24, 0.9);
    text-align: center;
    color: var(--color5);
}

