@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color0: #eeeeee;
  --color1: #0a0a0a;
  --color2: #12151e;
  --color3: #a493fa;
  --color4: #f53acc;
  --color5: #f1bf35;
  --color6: #52ec52;
  --color7: linear-gradient(90deg, rgb(12, 5, 43) 30%, rgb(32, 6, 39) 100%);
  --color8: #ffffff;

}

a {
  text-decoration: none;
  color: var(--color0);
}

ul {
  list-style: none;
}

body {
  font-family: 'Poppins', sans-serif;
  /* background: rgb(14, 4, 39); */
  background: linear-gradient(90deg, rgb(26, 5, 32) 30%, rgb(7, 3, 24) 100%);
  color: var(--color0);
}