.contenedor_inicio {
    width: 1000px;
    margin: 0 auto;
}

.TarjetaUltimos_grid {
    margin: 0 auto;
    /* width: 800px; */

    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 20px;
}

.terminados_grid {
    margin: 0 auto;
    /* width: 800px; */

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
}

.tarjetas_grid {
    margin: 0 auto;
    /* width: 800px; */

    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 10px;
}

@media screen and (max-width: 1200px) {
    .contenedor_inicio {
        width: 90%;
    }
}

@media screen and (max-width: 960px) {
    .contenedor_inicio {
        width: 100%;
    }

    .TarjetaUltimos_grid {
        width: 90%;
    }

    .terminados_grid {
        width: 90%;
        grid-template-columns: repeat(2, 1fr);
    }

    .tarjetas_grid {
        width: 90%;
    }
}

@media screen and (max-width: 768px) {
    .TarjetaUltimos_grid {
        grid-template-columns: repeat(4, 1fr);
    }

    .tarjetas_grid {
        grid-template-columns: repeat(4, 1fr);
    }
}

@media screen and (max-width: 576px) {
    .TarjetaUltimos_grid {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    .terminados_grid {
        width: 90%;
        grid-template-columns: repeat(1, 1fr);
    }

    .tarjetas_grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }
}

/* ************
*             *
*    carga    *
*             *
************ */

.carga {
    margin: 0 auto;

    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 20px;
}

.cuadro {
    border-radius: 5px;
    overflow: hidden;
}

.cuadro img {
    width: 100%;
    transform: scale(1.1);
    opacity: .1;
}

@media screen and (max-width: 1200px) {
    .carga {
        width: 100%;
    }

    .cuadro {
        width: 100%;
    }
}

@media screen and (max-width: 960px) {
    .carga {
        width: 90%;
    }

}

@media screen and (max-width: 768px) {
    .carga {
        grid-template-columns: repeat(4, 1fr);
    }

}

@media screen and (max-width: 576px) {
    .carga {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

}

/* **************
*               *
*    carga 2    *
*               *
************** */

.carga_2 {
    margin: 0 auto;

    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 10px;
}

.cuadro_2 {
    border-radius: 5px;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    gap: 20px 0;
}

.cuadro_2 img {
    width: 100%;
    transform: scale(1.05);
    opacity: .1;
}

.cuadro_2 .texto {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.cuadro_2 .texto div {
    height: 10px;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.1);
}

.carga_2 .boton {
    grid-column: 3/5;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.carga_2 .boton div {
    width: 22px;
    height: 32px;
    background: rgba(255, 255, 255, 0.1);
}

@media screen and (max-width: 1200px) {
    .carga_2 {
        width: 100%;
    }

    .cuadro_2 {
        width: 100%;
    }
}

@media screen and (max-width: 960px) {
    .carga_2 {
        width: 90%;
    }

}

@media screen and (max-width: 768px) {
    .carga_2 {
        grid-template-columns: repeat(4, 1fr);
    }

    .carga_2 .boton {
        grid-column: 2/4;
    }

}

@media screen and (max-width: 576px) {
    .carga_2 {
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }

    .carga_2 .boton {
        grid-column: 1/3;
    }

}