.separador {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}

.titulo {
    color: var(--color5);
    padding: 20px 0;
    text-align: start;

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.mas {
    background: var(--color5);
    padding: 2px 10px;
    border-radius: 8px;
    color: black;
    cursor: pointer;

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

@media screen and (max-width: 960px) {
    .separador {
        width: 90%;
    }

    .titulo {
        font-size: 1.3rem;
    }

}