.solido {
    height: 75px;
    width: 100%;
}

.menu_principal {
    width: 100%;

    display: flex;
    justify-content: center;
}

.menu_header {
    position: fixed;
    top: 2;
    z-index: 5;
    height: 75px;
    width: 1000px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background: var(--color7);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
}

.menu_header h1 {
    color: var(--color0);
}

.menu_header b {
    color: var(--color5);
}

.lista {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
}

.lista li {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
}

.lista li svg {
    width: 18px;
    height: 18px;
}

.lista li a {
    padding: 3px 6px;
    font-weight: 500;
    color: var(--color0);
    border-radius: 2px;

    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    gap: 6px;
}

.barra_icono {
    display: none;
}

.barra_a {
    position: absolute;
    width: 35px;
    height: 5px;
    background: var(--color5);
    border-radius: 2px;
    transform: translateY(-10px);
    transition: .2s;
}

.barra_b {
    position: absolute;
    width: 35px;
    height: 5px;
    background: var(--color5);
    border-radius: 2px;
    transform: translateY(0px);
    transition: .2s;
}

.barra_c {
    position: absolute;
    width: 35px;
    height: 5px;
    background: var(--color5);
    border-radius: 2px;
    transform: translateY(10px);
    transition: .2s;
}

.fondo {
    position: fixed;
    z-index: 2;
    width: 100vw;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    transition: .6s ease;
}

@media screen and (max-width: 1200px) {
    .menu_header {
        width: 100%;
    }
}

@media screen and (max-width: 768px) {
    .lista {
        position: absolute;
        top: 85px;
        left: -550px;
        padding: 50px;
        height: 35vh;
        width: 55vw;
        flex-direction: column;
        gap: 2rem;
        transition: .3s;
        opacity: .6;
        background: var(--color5);
    }

    .lista li a {
        font-size: 1.5rem;
        color: var(--color1);
        gap: 12px;
    }

    .lista li svg {
        width: 28px;
        height: 28px;
    }

    .barra_icono {
        width: 45px;
        height: 30px;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }

    .activar_menu {
        left: 10px;
        opacity: 1;
    }

    .barra_icono.cambiar div:first-of-type {
        transform: rotate(45deg);
    }

    .barra_icono.cambiar div:nth-last-child(2) {
        opacity: 0;
    }

    .barra_icono.cambiar div:last-of-type {
        transform: rotate(-45deg);
    }
}