.terminados {
    display: flex;
    flex-direction: row;
    gap: 15px;
}

.portada {
    width: 120px;
    /* border: 1px solid seagreen; */
    border-radius: 5px;

    flex-shrink: 0;

    display: flex;
    align-items: center;
    justify-content: center;
}

.portada img {
    width: 100%;
    border-radius: 5px;
    transition: all 0.6s ease;
    cursor: pointer;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.4);
}

.portada img:hover {
    transform: scale(1.05);
}

.informacion {
    /* border: 1px solid seagreen; */
    flex-shrink: 1;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.informacion h4 {
    width: 100%;
    font-size: 0.9rem;
    margin-bottom: 2px;
}

.informacion p {
    font-size: 0.8rem;
}

.generos {
    cursor: pointer;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 5px 10px;
}

.generos p {
    font-size: .7rem;
    background: var(--color4);
    border-radius: 8px;
    padding: 2px 5px;
}