.paginacion {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.paginacion button {
    border: none;
    padding: 8px;
    background: var(--color0);
    cursor: pointer;
}

.paginacion button.activo {
    background: var(--color5);
}